import React, { ChangeEvent, CSSProperties } from 'react';
import './App.css';
import { SimRenderer } from './SimRenderer';
import { CGAOptions, CGASolver } from './Solver';
import { Solution } from "./Solution";
import { LanderState } from './Simulator';
import { Point, MyUtils, CancellationToken } from './Utils';

// TODO: 
// considerare il carburante per la funzione di fitting

enum ELanderStateProp {
  x, y, angle,
  hSpeed, vSpeed,
  fuel, power
}
enum EGAOptions {
  PopulationCount, GenesCount,
  MutationProbability, ElitismPercent
}



class CLocalStorageManager {
  static getStartingLanderState(): LanderState {
    let landerStateFromStorage = localStorage.getItem("landerState");
    let landerState = new LanderState(2000, 2000, 0, 0, -20, 1000, 10);
    if (landerStateFromStorage) {
      const fields = landerStateFromStorage.split("|");
      // "x,y,angle,hSpeed,vSpeed,fuel,power"
      if (fields.length !== 7) return landerState;
      landerState = new LanderState(
        Number(fields[0]), Number(fields[1]), Number(fields[2]),
        Number(fields[3]), Number(fields[4]),
        Number(fields[5]), Number(fields[6]));
    }
    return landerState;
  }

  static saveStartingLanderState(landerState: LanderState) {
    const x = landerState.x;
    const y = landerState.y;
    const angle = landerState.angle;
    const hSpeed = landerState.hSpeed;
    const vSpeed = landerState.vSpeed;
    const fuel = landerState.fuel;
    const power = landerState.power;
    localStorage.setItem("landerState", `${x}|${y}|${angle}|${hSpeed}|${vSpeed}|${fuel}|${power}`);
  }

  static getStartingGAOptions(): CGAOptions {
    let GAOptionsFromStorage = localStorage.getItem("GAOptions");
    let GAOptions = new CGAOptions(20, 100, 0.10, 0.10);
    if (GAOptionsFromStorage) {
      const fields = GAOptionsFromStorage.split("|");
      // "PopulationCount|GenesCount|MutationProbability|ElitismPercent"
      if (fields.length !== 4) return GAOptions;
      GAOptions = new CGAOptions(Number(fields[0]), Number(fields[1]), Number(fields[2]), Number(fields[3]));
    }
    return GAOptions;
  }

  static saveStartingGAOptions(GAOptions: CGAOptions) {
    const PopulationCount = GAOptions.PopulationCount;
    const GenesCount = GAOptions.GenesCount;
    const MutationProbability = GAOptions.MutationProbability;
    const ElitismPercent = GAOptions.ElitismPercent;
    localStorage.setItem("GAOptions", `${PopulationCount}|${GenesCount}|${MutationProbability}|${ElitismPercent}`);
  }

  static parseTerrainPoints(text: string): Point[] {
    let newTerrain: Point[] = [];
    const fields = text.split(",");
    if (fields.length % 2 === 1) return [];
    for (let i = 0; i < fields.length / 2; i++) {
      const [x, y] = [Number(fields[i * 2].trim()), Number(fields[i * 2 + 1].trim())];
      newTerrain.push(new Point(x, y));
    }
    return newTerrain;
  }

  static terrainPointToString(points: Point[]): string {
    let terrainPointsText = "";
    for (let i = 0; i < points.length; i++) {
      const point = points[i];
      terrainPointsText += `${point.x}, ${point.y}`;
      if (i < points.length - 1) {
        terrainPointsText += `,\n`;
      }
    }
    return terrainPointsText;
  }

  static getStartingTerrain(): Point[] {
    let pointsFromStorage = localStorage.getItem("terrainPoints");
    let points = [
      new Point(0, 1500),
      new Point(2000, 1700),
      new Point(1000, 500),
      new Point(2000, 500),
      new Point(5000, 1500),
      new Point(6000, 1500),
      new Point(7000, 1000)];
    if (pointsFromStorage) {
      // "x1,y1,...,xn,yn"
      points = CLocalStorageManager.parseTerrainPoints(pointsFromStorage);
    }
    return points;
  }

  static saveStartingTerrain(points: Point[]) {
    localStorage.setItem("terrainPoints", CLocalStorageManager.terrainPointToString(points));
  }
}

class AppState {
  constructor(
    public solutionDescr: string,
    public generationDescr: string,
    public landerState: LanderState,
    public terrainPoints: Point[],
    public GAOptions: CGAOptions,
  ) { }
}
class App extends React.Component<{}, AppState> {

  private canvasRef: React.RefObject<HTMLCanvasElement>;
  private renderer!: SimRenderer;
  private cancellationTokenForSimulation: CancellationToken;

  constructor(props: {}) {
    super(props);
    this.canvasRef = React.createRef<HTMLCanvasElement>();
    this.cancellationTokenForSimulation = new CancellationToken();
    this.state = {
      solutionDescr: "",
      generationDescr: "",
      landerState: CLocalStorageManager.getStartingLanderState(),
      terrainPoints: CLocalStorageManager.getStartingTerrain(),
      GAOptions: CLocalStorageManager.getStartingGAOptions(),
    };
  }

  async componentDidMount() {
    await this.setupRendererForSimulation();
  }

  async setupRendererForSimulation() {
    const ctx = this.canvasRef.current?.getContext("2d")!;
    this.renderer = new SimRenderer(ctx, 7000, 3000);
    this.renderer.setTerrain(this.state.terrainPoints);
    this.renderer.drawLander(this.state.landerState);
  }

  private onLanderStateChanged = (prop: ELanderStateProp) => (e: React.ChangeEvent<HTMLInputElement>) => {
    let value = e.target.valueAsNumber;
    let newState = this.state.landerState.clone();
    switch (prop) {
      case ELanderStateProp.x: newState.x = value; break;
      case ELanderStateProp.y: newState.y = value; break;
      case ELanderStateProp.hSpeed: newState.hSpeed = value; break;
      case ELanderStateProp.vSpeed: newState.vSpeed = value; break;
      case ELanderStateProp.angle: newState.angle = value; break;
      case ELanderStateProp.fuel: newState.fuel = value; break;
      case ELanderStateProp.power: newState.power = value; break;
    }
    CLocalStorageManager.saveStartingLanderState(newState);
    this.renderer.drawLander(newState);
    this.setState({ landerState: newState })
  };

  private onGAOptionsChanged = (prop: EGAOptions) => (e: React.ChangeEvent<HTMLInputElement>) => {
    let value = e.target.valueAsNumber;
    let newOptions = this.state.GAOptions;
    switch (prop) {
      case EGAOptions.PopulationCount: newOptions.PopulationCount = value; break;
      case EGAOptions.GenesCount: newOptions.GenesCount = value; break;
      case EGAOptions.MutationProbability: newOptions.MutationProbability = value; break;
      case EGAOptions.ElitismPercent: newOptions.ElitismPercent = value; break;
    }
    CLocalStorageManager.saveStartingGAOptions(newOptions)
    this.setState({ GAOptions: newOptions })
  };

  private lastSolution!: Solution;

  private onGenerationReady = async (gen: Solution[], genNumber: number, avgScore: number, elapsedTimeMS: number) => {
    let message = `#Gen: ${genNumber.toFixed(0).padStart(5, "0")}`;
    message += `-> MaxScore: ${avgScore.toFixed(1).padStart(5, "0")},`;
    message += ` [${elapsedTimeMS.toFixed(0).padStart(8, "0")}ms]`;

    this.setState({ generationDescr: message });
    let alpha = 0.5;
    this.renderer.drawTrajectory(gen[0], `#${(genNumber % 255).toString(16)}8FFF`, alpha);
  };

  private startSimulation = async () => {
    this.renderer.drawLander(this.state.landerState);
    const renderer = this.renderer;
    let solver = new CGASolver(this.state.GAOptions, this.state.landerState, this.state.terrainPoints,
      this.onGenerationReady, this.cancellationTokenForSimulation);
    const finalSolution = await solver.getSolution(-1, 100);
    this.lastSolution = finalSolution;

    this.renderer.drawLander(this.state.landerState);

    if (finalSolution.perfectLanding()) {
      renderer.drawTrajectory(finalSolution, "#00FF00");
    } else {
      renderer.drawTrajectory(finalSolution, "#FF0000");
    }

    this.setState({ solutionDescr: finalSolution.toString() })
  };

  private stopSimulation = () => {
    this.cancellationTokenForSimulation.cancel();
  };

  private animateSolution = async () => {
    if (this.lastSolution) {
      const states = this.lastSolution.lander.stateHistory;
      for (let i = 0; i < states.length; i++) {
        if (i > 0) {
          this.renderer.drawLander(states[i], states[i - 1], 100);
        } else {
          this.renderer.drawLander(states[i]);
        }
        // this.renderer.drawTrajectory(this.lastSolution, "#0000FF");
        await MyUtils.sleep(100);
      }
    }
  };

  private onTerrainChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newTerrainText = e.target.value.trim();
    const newTerrain = CLocalStorageManager.parseTerrainPoints(newTerrainText);
    if (newTerrain.length === 0) return;
    CLocalStorageManager.saveStartingTerrain(newTerrain);
    this.renderer.setTerrain(newTerrain);
    this.renderer.drawLander(this.state.landerState);
    this.setState({ terrainPoints: newTerrain });
  };

  private terrainTextElement = () => {
    const terrainPointsText = CLocalStorageManager.terrainPointToString(this.state.terrainPoints);
    return (<textarea
      style={{ width: "100%", height: "100%", resize: "none" }}
      value={terrainPointsText} onChange={this.onTerrainChange} ></textarea>);
  }

  render() {
    const textBoxWidth = "50px";
    const flexRowStyle: CSSProperties = { display: "flex", flexDirection: "row", columnGap: "5px", justifyContent: "space-around" };
    const buttonStyle: CSSProperties = { width: "auto", fontSize: "20px" };
    return (
      <div className="App" style={{
        fontFamily: "consolas", height: "100vh", display: "grid",
        gridTemplateColumns: "300px 1fr", gridTemplateRows: "50px 1fr 50px 100px"
      }}>
        <div style={{ alignSelf: "center", gridRow: "1", gridColumn: "1/3", fontSize: "20px", fontWeight: "bold" }}>
          Mars Lander - A Genetic Algorithm approach
        </div>
        <div style={{
          gridRow: "2/5", gridColumn: "1", margin: "10px",
          display: "flex", flexDirection: "column", rowGap: "10px", alignItems: "flex-start"
        }}>
          <div style={{ fontWeight: "bold" }}>Lander parameters</div>
          <div>Position</div>
          <div style={{ ...flexRowStyle }}>
            <span>x</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.x}
              onChange={this.onLanderStateChanged(ELanderStateProp.x)} ></input>
            <span>y</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.y}
              onChange={this.onLanderStateChanged(ELanderStateProp.y)} ></input>
            <span>∠</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.angle}
              onChange={this.onLanderStateChanged(ELanderStateProp.angle)} ></input>
          </div>
          <div>Speed</div>
          <div style={{ ...flexRowStyle }}>
            <span>→</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.hSpeed}
              onChange={this.onLanderStateChanged(ELanderStateProp.hSpeed)} ></input>
            <span>↑</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.vSpeed}
              onChange={this.onLanderStateChanged(ELanderStateProp.vSpeed)} ></input>
          </div>
          <div>Engine</div>
          <div style={{ ...flexRowStyle }}>
            <span>fuel</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.fuel}
              onChange={this.onLanderStateChanged(ELanderStateProp.fuel)} ></input>
            <span>power</span>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.landerState.power}
              onChange={this.onLanderStateChanged(ELanderStateProp.power)} ></input>
          </div>
          <div style={{ fontWeight: "bold" }}>Terrain [x1,y1,...,xn,yn]</div>
          {this.terrainTextElement()}
          <div style={{ fontWeight: "bold" }}>GA parameters</div>
          <div style={{ display: "grid", gridTemplate: "1fr 1fr 1fr 1fr / 1fr auto", alignItems: "center" }}>
            <div>Trajectories [chromosomes] / generation</div>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.GAOptions.PopulationCount}
              onChange={this.onGAOptionsChanged(EGAOptions.PopulationCount)} ></input>
            <div>Maneuvers [genes] count</div>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.GAOptions.GenesCount}
              onChange={this.onGAOptionsChanged(EGAOptions.GenesCount)} ></input>
            <div>Mutation probability %</div>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.GAOptions.MutationProbability}
              onChange={this.onGAOptionsChanged(EGAOptions.MutationProbability)} ></input>
            <div>Elitism %</div>
            <input type='number' style={{ width: textBoxWidth }}
              value={this.state.GAOptions.ElitismPercent}
              onChange={this.onGAOptionsChanged(EGAOptions.ElitismPercent)} ></input>
          </div>
          <div style={{ fontWeight: "bold" }}>Simulation</div>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <button style={buttonStyle} onClick={this.startSimulation} >▶ play</button>
            <button style={buttonStyle} onClick={this.stopSimulation} >■ stop</button>
            <button style={buttonStyle} onClick={this.animateSolution} ><span role='img' aria-label='clapper board'>🎬 animate</span></button>
          </div>
        </div>
        <canvas ref={this.canvasRef}
          style={{ width: "100%", gridRow: "2", gridColumn: "2" }} width="7000" height="3000"></canvas>
        <div style={{ fontSize: "20px", gridRow: "3", gridColumn: "2" }} >{this.state.generationDescr}</div>
        <div style={{ fontSize: "20px", whiteSpace: "break-spaces", gridRow: "4", gridColumn: "2" }} >{this.state.solutionDescr}</div>
      </div>
    );
  }
}

export default App;
