import { LanderAction, LanderState, Lander } from "./Simulator";
import { Point, MyUtils } from "./Utils";
import { CGAOptions } from "./Solver";

// https://www.codingame.com/blog/genetic-algorithm-mars-lander/?utm_source=codingame&utm_medium=details-page&utm_campaign=cg-blog&utm_content=mars-lander-2

export class Solution {
  private Score: number = Number.MIN_SAFE_INTEGER;

  constructor(
    public lander: Lander,
    private landingSites: Point[],
    private readonly options: CGAOptions
  ) { }

  toString(): string {
    let result = `Atterraggio ${this.perfectLanding() ? "COMPLETATO" : "NON RIUSCITO"}\n`;
    result += `Azioni:${this.lander.validStatesCount} Punteggio: ${this.getScore().toFixed(0)}\n`;
    result += `Lander: ${this.lander.currentState.toString()}`;
    return result;
  }

  static createStartingSolution(
    initialState: LanderState, terrainPoints: Point[],
    landingSites: Point[], GAOptions: CGAOptions
  ): Solution {
    let actions = [LanderAction.createFromLanderState(initialState)];
    let currAction = actions[0];
    for (let i = 0; i < GAOptions.GenesCount; i++) {
      currAction = currAction.getNextRandomAction();
      actions.push(currAction);
    }
    let lander = new Lander(initialState, actions, terrainPoints);
    return new Solution(lander, landingSites, GAOptions);
  }

  perfectLanding(): boolean {
    if (this.lander.fuelEnded)
      return false;
    if (!this.lander.touchDown)
      return false;
    if (this.lander.currentState.absVSpeed > 40)
      return false;
    if (this.lander.currentState.absHSpeed > 20)
      return false;
    if (this.lander.currentState.absAngle > 0)
      return false;
    return true;
  }

  getScore(): number {
    if (this.lander.isScoreDirty) {
      this.lander.simulateLanding();
      this.Score = this.evaluateScore();
      this.lander.isScoreDirty = false;
    }
    return this.Score;
  }

  private evaluateScore(): number {
    // l'atterraggio ha successo se:
    // - il terreno è piatto
    // - il lander è in posizione verticale (angle = 0°)
    // - la velocità verticale deve avere un modulo <= 40m/s
    // - la velocità orizzontale deve avere un modulo <= 20m/s

    // (TODO) Il punteggio è maggiore se:
    // - viene risparmiato più carburante
    // - vengono eseguite meno manovre

    let nearestLandingSite = new Point(0, 0);
    let minHorDistance = Number.MAX_SAFE_INTEGER;
    for (let i = 0; i < this.landingSites.length; i++) {
      const currDist = Math.abs(this.landingSites[i].x - this.lander.currentState.x);
      if (currDist < minHorDistance) {
        minHorDistance = currDist;
        nearestLandingSite = this.landingSites[i];
      }
    }

    let score = 0;
    const MIN_LANDING_EXTEND = 1000;
    const HALF_MIN_LANDING_EXTEND = MIN_LANDING_EXTEND * 0.5;
    const horDistanceFromSite = minHorDistance - HALF_MIN_LANDING_EXTEND;
    const verDistanceFromSite = this.lander.currentState.y - nearestLandingSite.y;
    if (horDistanceFromSite > 0 || verDistanceFromSite > 0) {
      score += 200;
      // zona di atterraggio mancata
      if (horDistanceFromSite > 0)
        score += horDistanceFromSite;
      if (verDistanceFromSite > 0)
        score += verDistanceFromSite;
    } else {
      // atterraggio nella zona giusta, verifico stato del lander
      let absVSpeed = this.lander.currentState.absVSpeed - 40;
      let absHSpeed = this.lander.currentState.absHSpeed - 20;
      let absTilt = this.lander.currentState.absAngle;
      if (absVSpeed > 0)
        score += absVSpeed;
      if (absHSpeed > 0)
        score += absHSpeed;
      if (absTilt > 0)
        score += absTilt;
    }

    const max_score = 1000;
    // this.Score = newScore;
    // this.Score = Math.max(newScore, 0);
    // this.Score = 5000 - score;
    this.Score = max_score * Math.exp(-score / 500);
    return this.Score;
  }

  crossover(other: Solution): [Solution, Solution] {
    // Continuous Genetic Algorithm
    // https://towardsdatascience.com/continuous-genetic-algorithm-from-scratch-with-python-ff29deedd099
    let child1Actions: LanderAction[] = [];
    let child2Actions: LanderAction[] = [];
    let thisHistory = this.lander.actionHistory;
    let otherHistory = other.lander.actionHistory;
    let mutationFactor = 0;
    if (Math.random() < this.options.MutationProbability) {
      mutationFactor = Math.random();
    }
    for (let i = 0; i < this.options.GenesCount; i++) {
      child1Actions.push(new LanderAction(
        mutationFactor * thisHistory[i].angle + (1 - mutationFactor) * otherHistory[i].angle,
        mutationFactor * otherHistory[i].power + (1 - mutationFactor) * thisHistory[i].power
      ));
      child2Actions.push(new LanderAction(
        mutationFactor * otherHistory[i].angle + (1 - mutationFactor) * thisHistory[i].angle,
        mutationFactor * thisHistory[i].power + (1 - mutationFactor) * otherHistory[i].power
      ));
    }
    let child1Lander = this.lander.cloneLander(child1Actions);
    let child2Lander = this.lander.cloneLander(child2Actions);
    return [
      new Solution(child1Lander, this.landingSites, this.options),
      new Solution(child2Lander, this.landingSites, this.options)
    ];
  }

  mutate() {
    let newActions: LanderAction[] = [];
    let prevAction = LanderAction.createFromLanderState(this.lander.initialState);
    let rndIndex = MyUtils.random(0, this.options.GenesCount - 1);
    for (let i = 0; i < this.options.GenesCount; i++) {
      let newAction = this.lander.actionHistory[i];
      if (i === rndIndex) {
        newAction = prevAction.getNextRandomAction();
      }
      newActions.push(newAction);
      prevAction = newAction;
    }
    this.lander = this.lander.cloneLander(newActions);
  }

}
