import { Solution } from "./Solution";
import { LanderState } from "./Simulator";
import { Point } from "./Utils";

export class SimRenderer {

  private landerImage: HTMLImageElement;
  private landerImageReady: boolean;
  private terrainPoints: Point[];

  constructor(
    private canvasCtx: CanvasRenderingContext2D,
    private canvasWidth: number,
    private canvasHeight: number
  ) {
    this.landerImageReady = false;
    this.landerImage = new Image(281, 402);
    this.canvasCtx.imageSmoothingEnabled = true;
    this.canvasCtx.imageSmoothingQuality = "high";
    this.terrainPoints = [];
  }

  private getCanvasY(y: number) {
    return this.canvasHeight - y;
  }

  setTerrain(terrainPoints: Point[]) {
    this.terrainPoints = terrainPoints;
  }

  drawTerrain() {
    // sfondo
    const ctx = this.canvasCtx;
    ctx.fillStyle = "#997751";
    ctx.fillRect(0, 0, this.canvasWidth, this.canvasHeight);

    let terrainWithBackground = this.terrainPoints.slice();
    terrainWithBackground.push(new Point(this.canvasWidth, 0));
    terrainWithBackground.push(new Point(0, 0));

    ctx.strokeStyle = "#602D1C";
    ctx.lineWidth = 10;
    ctx.fillStyle = "#602D1C";
    ctx.beginPath();
    ctx.moveTo(0, 0);
    for (let point of terrainWithBackground) {
      ctx.lineTo(point.x, this.getCanvasY(point.y));
    }
    ctx.closePath();
    ctx.stroke();
    ctx.fill()

    let [x, y] = [0, 0]
    for (let point of this.terrainPoints) {
      if (y === point.y && Math.abs(x - point.x) >= 1000) {
        ctx.lineWidth = 20;
        ctx.strokeStyle = "#39A030";
        ctx.beginPath();
        ctx.moveTo(x, this.getCanvasY(y));
        ctx.lineTo(point.x, this.getCanvasY(point.y));
        ctx.closePath();
        ctx.stroke();
      }
      [x, y] = [point.x, point.y]
    }

    this.drawLabels();
  }

  drawLander(currState: LanderState, prevState: LanderState | null = null, animDurationMS = 20) {
    const [imgW, imgH] = [200, 286];
    const ctx = this.canvasCtx;
    const animate = !!prevState;

    const contextDraw = (x: number, y: number, angle: number) => {
      this.drawTerrain();
      ctx.translate(x, y - imgH * 0.5);
      ctx.rotate(angle);
      ctx.drawImage(this.landerImage, -imgW * 0.5, -imgH * 0.5, imgW, imgH);
      ctx.rotate(-angle);
      ctx.translate(-x, -y + imgH * 0.5);
    }

    const [currX, currY] = [currState.x, this.getCanvasY(currState.y)];
    const currAngle = -currState.angle / 180 * Math.PI;

    const drawAction = () => {
      if (animate) {
        const startTime = performance.now();
        const prevAngle = -prevState.angle / 180 * Math.PI;
        const [prevX, prevY] = [prevState.x, this.getCanvasY(prevState.y)];

        const renderLoop = (currTime: DOMHighResTimeStamp) => {
          const percent = (currTime - startTime) / animDurationMS;
          if (percent > 1) {
            contextDraw(currX, currY, currAngle);
            return;
          }
          const x = prevX + (currX - prevX) * percent;
          const y = prevY + (currY - prevY) * percent;
          const angle = prevAngle + (currAngle - prevAngle) * percent;
          contextDraw(x, y, angle);
          window.requestAnimationFrame(renderLoop);
        }
        window.requestAnimationFrame(renderLoop);
      } else {
        contextDraw(currX, currY, currAngle);
      }
    }

    if (!this.landerImageReady) {
      this.landerImage.onload = (e: Event): any => {
        drawAction();
        this.landerImageReady = true;
      };
      this.landerImage.src = require("./media/mars_lander.png");
    } else {
      drawAction();
    }
  }

  drawTrajectory(solution: Solution, strokeColor: string = "#FF0000", alpha: number = 1) {
    const ctx = this.canvasCtx;
    let firstPoint = solution.lander.stateHistory[0];
    let [x, y] = [firstPoint.x, firstPoint.y]
    ctx.strokeStyle = strokeColor;
    ctx.lineWidth = 10;
    ctx.globalAlpha = alpha;
    for (let point of solution.lander.stateHistory) {
      ctx.beginPath();
      ctx.moveTo(x, this.canvasHeight - y);
      ctx.lineTo(point.x, this.canvasHeight - point.y);
      ctx.closePath();
      ctx.stroke();
      [x, y] = [point.x, point.y]
    }
    ctx.globalAlpha = 1;
  }

  drawLabels() {
    const ctx = this.canvasCtx;
    ctx.fillStyle = "#ffd966";
    ctx.font = "80px sans-serif";
    {
      let currLabelX = 500;
      while (currLabelX < this.canvasWidth) {
        ctx.fillText(currLabelX.toString(), currLabelX, this.getCanvasY(50));
        ctx.fillRect(currLabelX, this.getCanvasY(50), 10, 50);
        currLabelX += 500;
      }
    }
    {
      let currLabelY = 500;
      while (currLabelY < this.canvasHeight) {
        ctx.fillText(currLabelY.toString(), 50, this.getCanvasY(currLabelY));
        ctx.fillRect(0, this.getCanvasY(currLabelY), 50, 10);
        currLabelY += 500;
      }
    }
  }

}