import { LanderState } from "./Simulator";
import { Solution } from "./Solution";
import { Point, MyUtils, CancellationToken } from "./Utils";

export class CGAOptions {
  constructor(
    public PopulationCount: number,
    public GenesCount: number,
    public MutationProbability: number,
    public ElitismPercent: number) { }
}

export class CGASolver {
  private readonly landingSites: Point[];

  constructor(
    private readonly GAOptions: CGAOptions,
    private initialState: LanderState,
    private terrainPoints: Array<Point>,
    public generationReady: (gen: Solution[], genNumber: number, avgScore: number, elapsedTime: number) => void,
    private cancellationToken: CancellationToken,
  ) {
    this.landingSites = [];
    let [x, y] = [0, 0];
    for (let point of terrainPoints) {
      // cerco il sito di atterraggio
      if (y === point.y && Math.abs(x - point.x) >= 1000) {
        let landingY = y;
        let landingX = Math.round(x + Math.abs(x - point.x) * 0.5);
        this.landingSites.push(new Point(landingX, landingY));
      }
      [x, y] = [point.x, point.y];
    }
  }

  private createPopulation(): Solution[] {
    let result: Solution[] = [];
    for (let i = 0; i < this.GAOptions.PopulationCount; i++) {
      result.push(Solution.createStartingSolution(
        this.initialState, this.terrainPoints, this.landingSites, this.GAOptions));
    }
    result.sort((a, b) => (b.getScore() - a.getScore()));
    return result;
  }

  private selectElitism(population: Solution[]): Solution[] {
    const eliteIndex = Math.round(this.GAOptions.PopulationCount * this.GAOptions.ElitismPercent);
    return population.slice(0, eliteIndex);
  }

  private rouletteWheelSelection(population: Solution[]): [Solution, Solution] {
    let scoreSum = 0;
    let scores: number[] = [];

    for (let i = 0; i < population.length; i++) {
      let currScore = population[i].getScore();
      scores.push(currScore);
      scoreSum += currScore;
    }
    // somma cumulativa
    for (let i = 0; i < population.length; i++) {
      for (let j = i + 1; j < scores.length; j++) {
        scores[i] += scores[j];
      }
    }
    // normalizzazione
    for (let i = 0; i < scores.length; i++) {
      scores[i] /= scoreSum;
    }
    // ricerca due genitori (primo individuo con punteggio maggiore di un valore casuale)
    const parent1RndIndex = Math.random();
    let parent1Index = 0;
    for (let i = scores.length - 1; i >= 0; i--) {
      if (scores[i] > parent1RndIndex) {
        parent1Index = i;
        break;
      }
    }
    let parent2Index = parent1Index === 0 ? scores.length - 1 : 0;
    let parent2RndIndex = Math.random();
    for (let i = scores.length - 1; i >= 0; i--) {
      if (scores[i] > parent2RndIndex) {
        parent2Index = i;
        break;
      }
    }

    return [population[parent1Index], population[parent2Index]]
  }

  private populateNextGenerationAndSortByScore(sortedPop: Solution[]): Solution[] {
    let selected: Solution[] = [];
    // crossover fino a riempire la nuova pool
    while (selected.length < sortedPop.length) {
      let parents = this.rouletteWheelSelection(sortedPop);
      let children = parents[0].crossover(parents[1]);
      selected.push(...children);
    }
    // prendo l'elite dalla popolazione precedente e la copio nella nuova pool
    let elite = this.selectElitism(sortedPop);
    for (let i = 0; i < elite.length; i++) {
      selected[i] = elite[i];
    }

    // muto ogni nuovo individuo
    selected.forEach((child) => child.mutate());
    // ordino ancora dopo la mutazione
    selected.sort((a, b) => (b.getScore() - a.getScore()));

    return selected;
  }

  public async getSolution(MAX_TIME_MS = -1, callbackIntervalMS = 200): Promise<Solution> {
    let simulationEnded = false;
    const startTime = Date.now();
    let elapsedTime = 0;
    let lastRenderingTime = Date.now();
    let elapsedTimeFromLastRendering = 0;

    let maxScore = Number.MIN_SAFE_INTEGER;

    let population = this.createPopulation();
    let result = population[0];
    let genCount = 0;

    this.cancellationToken.reset();
    while (!simulationEnded && !this.cancellationToken.isCancelled()) {
      // ogni ciclo è una generazione
      population = this.populateNextGenerationAndSortByScore(population);
      // gli individui sono già ordinati per punteggio
      let currScore = population[0].getScore();
      if (currScore > maxScore) {
        maxScore = currScore;
        result = population[0];
      }
      genCount += 1;
      elapsedTime = (Date.now() - startTime);
      simulationEnded = result.perfectLanding();
      if (MAX_TIME_MS > 0) {
        simulationEnded = simulationEnded || (elapsedTime > MAX_TIME_MS);
      }

      // callback per renderer
      elapsedTimeFromLastRendering = (Date.now() - lastRenderingTime);
      if (elapsedTimeFromLastRendering > callbackIntervalMS) {
        lastRenderingTime = Date.now();
        await this.generationReady(population, genCount, maxScore, elapsedTime);
        await MyUtils.sleep(1);
      }
    }
    this.generationReady(population, genCount, maxScore, elapsedTime);
    return result;
  }

}

