import { MyUtils, Point } from "./Utils";

export class LanderAction {
  constructor(
    // rotazione del lander in gradi (verticale = 0) [-90...90], +/-15° rispetto al turno precedente
    public angle: number,
    // accelerazione (m/s^2) [0,1,2,3,4], +/-1 rispetto al turno precedente
    public power: number,
  ) { }

  static createFromLanderState(state: LanderState): LanderAction {
    return new LanderAction(state.angle, state.power);
  }

  getNextRandomAction(): LanderAction {
    const rot = this.angle;
    const pow = this.power;
    const minRot = Math.max(rot - 10, -90);
    const maxRot = Math.min(rot + 10, 90);
    const minPow = Math.max(pow - 1, 0);
    const maxPow = Math.min(pow + 1, 10);
    return new LanderAction(MyUtils.random(minRot, maxRot), MyUtils.random(minPow, maxPow));
  }

}

export class LanderState {
  constructor(
    // posizione lander
    public x: number,
    public y: number,
    // rotazione del lander in gradi (verticale = 0) [-90...90], +/-15° rispetto allo stato attuale
    public angle: number,
    // velocità nei due assi (m/s)
    public hSpeed: number,
    public vSpeed: number,
    // quantità di benzina (l) [0...2000]
    public fuel: number,
    // accelerazione (m/s^2) [0,1,2,3,4], +/-1 rispetto allo stato attuale
    public power: number,
  ) { }

  public get absVSpeed(): number {
    return Math.abs(this.vSpeed);
  }
  public get absHSpeed(): number {
    return Math.abs(this.hSpeed);
  }
  public get absAngle(): number {
    return Math.abs(this.angle);
  }

  clone(): LanderState {
    return new LanderState(
      this.x,
      this.y,
      this.angle,
      this.hSpeed,
      this.vSpeed,
      this.fuel,
      this.power,
    );
  }

  toString(): string {
    let result = `x:${Math.round(this.x)} `;
    result += `y:${Math.round(this.y)} `;
    result += `angle:${Math.round(this.angle)} `;
    result += `hSpeed:${Math.round(this.hSpeed)} `;
    result += `vSpeed:${Math.round(this.vSpeed)} `;
    result += `fuel:${Math.round(this.fuel)} `;
    result += `power:${Math.round(this.power)}`;
    return result;
  }
}

export class Lander {
  private readonly GRAVITY = -3.711;
  public validStatesCount: number = -1;

  public readonly currentState: LanderState;
  public readonly stateHistory: LanderState[] = [];
  public isScoreDirty: boolean = true;

  // lo stato iniziale e le azioni compiute dal lander sono immutabili
  constructor(
    public readonly initialState: LanderState,
    public readonly actionHistory: LanderAction[],
    private terrainPoints: Point[]
  ) {
    this.currentState = initialState.clone();
    this.stateHistory.push(initialState);
  }

  private _touchDown: boolean = false;
  public get touchDown(): boolean {
    return this._touchDown;
  }
  private _fuelEnded: boolean = false;
  public get fuelEnded(): boolean {
    return this._fuelEnded;
  }

  cloneLander(actions: LanderAction[]): Lander {
    return new Lander(this.initialState, actions, this.terrainPoints);
  }

  simulateLanding() {
    for (let action of this.actionHistory) {
      this.simulateAction(action);
      if (this.touchDown || this.fuelEnded)
        return;
    }
  }

  private simulateAction(action: LanderAction) {
    this.currentState.angle = action.angle;
    this.currentState.power = action.power;

    const angleRad = this.currentState.angle / 180 * Math.PI;
    const acceleration = this.currentState.power;
    const vAcc = acceleration * Math.cos(-angleRad);
    const hAcc = acceleration * Math.sin(-angleRad);
    this.currentState.vSpeed += vAcc + this.GRAVITY;
    this.currentState.hSpeed += hAcc;

    this.currentState.fuel -= acceleration;
    this.currentState.x += this.currentState.hSpeed;
    this.currentState.y += this.currentState.vSpeed;

    this.stateHistory.push(this.currentState.clone())

    this.validStatesCount = this.stateHistory.length - 1;
    this.calcTrajectoryEnded();
    this.isScoreDirty = true;
  }

  private calcTrajectoryEnded() {
    if (this.currentState.fuel <= 0) {
      this._fuelEnded = true;
      return;
    }

    this._touchDown = false;
    let lastState = this.stateHistory[this.stateHistory.length - 1];
    let secondLastState = this.stateHistory[this.stateHistory.length - 2];

    // Aggiunte limiti laterali del canvas
    let terrainPointsCount = this.terrainPoints.length;
    let terrainPointsWithBounds = [new Point(0, 100000)];
    terrainPointsWithBounds.push(...this.terrainPoints);
    terrainPointsWithBounds.push(new Point(this.terrainPoints[terrainPointsCount - 1].x, 100000));

    for (let i = 0; i <= terrainPointsWithBounds.length - 2; i++) {
      const tp1 = terrainPointsWithBounds[i];
      const tp2 = terrainPointsWithBounds[i + 1];
      const intersection = MyUtils.intersects(
        secondLastState.x, secondLastState.y, lastState.x, lastState.y,
        tp1.x, tp1.y, tp2.x, tp2.y
      );
      if (intersection) {
        this._touchDown = true;
        return;
      }
    }
  }

}